
.fade-enter-active,.fade-leave-active{-webkit-transition:0.5s;transition:0.5s
}
.fade-enter,.fade-leave-to{opacity:0
}
.fade-move{-webkit-transition:-webkit-transform 1s;transition:-webkit-transform 1s;transition:transform 1s;transition:transform 1s, -webkit-transform 1s
}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,form,fieldset,input,textarea,p,blockquote,th,td{margin:0;padding:0
}
table{border-collapse:collapse;border-spacing:0
}
fieldset,img{border:0
}
address,caption,cite,code,dfn,em,strong,th,var{font-style:normal;font-weight:normal
}
ol,ul{list-style:none
}
caption,th{text-align:left
}
q:before,q:after{content:''
}
abbr,acronym{border:0
}
html,body{height:100%
}
input{background:transparent;border:none
}
input:focus,input:focus{outline:none !important
}
body{font-family:'Microsoft JhengHei',Arial;font-size:12px
}
a{outline:none;hlbr:expression(this.onFocus=this.blur());text-decoration:none
}
.clear{clear:both
}
img{vertical-align:top
}
html{-webkit-text-size-adjust:100%
}
.loading{position:fixed;width:100%;height:100%;top:0;left:0;margin-left:0;margin-top:0;z-index:9999;background-color:#012d92
}
.loading span{position:absolute;width:320px;height:34px;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);font-size:15px;line-height:34px;font-weight:normal;font-family:Arial,"Microsoft JhengHei";color:#fff;text-align:center;text-transform:uppercase;letter-spacing:3px
}
.wrapper{position:relative;width:100%;height:auto;float:left;left:0;right:0;top:0;bottom:0;margin:0
}



